/* @import "./vars.css"; */

html {
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
  background-color: #ffefd5;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

​img {
  display: block;
  max-width: 100%;
  height: auto;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline: none;
}

.__react_component_tooltip.type-dark {
  background: var(--bg-color-dark-blue);
  opacity: 1;
}
